import { mapState, mapActions } from 'vuex'
import { upyunUrl } from '../../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      oJson: ''
    }
  },
  onLoad (options) {
    this.getOrderDetailFn(options.id)
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapActions([
      'getOrderDetail'
    ]),
    // 获取订单详情
    getOrderDetailFn (id) {
      const data = {
        that: this,
        id
      }
      this.getOrderDetail(data).then((res) => {
        this.oJson = res
      })
    },
    // 复制订单号
    onCopyTextFn (text) {
      uni.setClipboardData({
        data: text,
        success () {
          uni.showToast({
            title: '复制成功'
          })
        }
      })
    }
  }
}
